import { createAsyncStore } from '@pfg/vue-async';
import { getExampleSuccess, getExampleUnauthorized } from '@/api/example';

const errorHandlers = {
  401: (error) => { console.log('401 error handler. error: ', error); },
};

const actionsData = [
  { actionType: 'getExampleSuccess', asyncFn: getExampleSuccess, errorHandlers },
  { actionType: 'getExampleUnauthorized', asyncFn: getExampleUnauthorized, errorHandlers },
];

export default {
  namespaced: false,
  ...createAsyncStore(actionsData),
};
